* {
	margin: 0;
	padding: 0;
}

body {
	color: white;
	width: 100vw;
	height: 100%;
	overflow: hidden;
}

button {
	background-color: rgb(88, 188, 228);
	color: white;
	z-index: 1;
	font-size: 1.4rem;
	width: 180px;
	height: 100px;
	border: 2px solid white;
	border-radius: 10px;
	margin-right: 50px;
	font-weight: bold;
}

.headerContent {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	row-gap: 70px;
	position: absolute;
	left: 1070px;
	top: 300px;
}

.container {
	position: relative;
	width: 100vw;
	height: 100vh;
}

#headerTitle {
	grid-column: 1/3;
}
#headerButtonExplore {
	grid-column: 1/2;
}

#headerButtonAbout {
	grid-column: 2/3;
}

video {
	z-index: 0;
}
